import React, { Component } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'

import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import CIcon from '@coreui/icons-react'
import $ from 'jquery'
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import conf from '../../../conf.js'
import Flatpickr from "react-flatpickr"
import { French } from "flatpickr/dist/l10n/fr.js";
import  { Redirect } from 'react-router-dom';


class Register extends Component {
  constructor(props) {
      super(props)
      this.state = {
        role:{},
        role_id:{},
        role_option:{},
        document:[
          {value:"CARTE D'IDENTE",label:"CARTE D'IDENTE"},
          {value:"PASSPORT",label:"PASSPORT"}
        ],
        document_id:"",
        ok:"",
        date:"",
        message:"Confirmation mot de passe non conforme !",
        message2:""
      }
      this.handleChangeDocument.bind(this) 
  }

  componentDidMount() {
  
    // document.getElementById("field_terms").setCustomValidity("Please indicate that you accept the Terms and Conditions");
    const myHeaders = new Headers();
    
     let  data=  JSON.parse(localStorage.getItem('token_auth'))
   $("#echec").css("display","none")
   $("#success").css("display","none")
   
   const requestOptions = {
    // headers: { 
    //   'Authorization':'Bearer '+data.token,

    // },

    'method':'GET'
   };


    fetch(conf.url+"user/role/roles",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
     

      try{
        if(data.length){
          data.map((el)=>{
            if(el.libelle=="VISITEUR")
            this.setState({role:el})
            
          })
        }else{
          this.setState({role:{}})
        }
       }catch(error){
        this.setState({role:{}})
       }

     
     }
     
    );
  }

  handleAddUser=()=>{

    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "username": $("#email").val(),
      "nom": $("#nom").val(),
      "prenom": $("#prenom").val(),
      "email":$("#email").val(),
      "roles":this.state.role,
      "adresse": $("#adresse").val(),
      "telephone": $("#telephone").val(),
      "password": $("#password").val(),
      "naissance":this.state.date,
      "numeroId": $("#numeroId").val(),
      "typeId": this.state.document_id,
      "monToken": " ",
      "resetPasswordToken": " ",
      "userDelete":false,
      "terrains":null,
      "reservations":null
    }
    var cb = document.getElementById('checkCGU');
    console.log("checkbox",cb.checked)
    var error=data.nom=="" || data.prenom=="" || data.email=="" || data.telephone==""|| data.password=="";
    

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        // 'Authorization':'Bearer '+token.token,
      },
      body: JSON.stringify(data)
    };

    if(error){
      this.setState({message:"Veuillez renseigner les champs obligatoires."})
      $("#echec").css("display","block")
      
    }
    else{
      if(($("#password").val()!=$("#password2").val() || $("#password2").val()=="")){
        this.setState({message:"Veuillez renseigner des mots de passe identique."})
        $("#echec").css("display","block")
      }
      else{

        fetch(conf.url+"user/register",requestOptions)
        .then((response) => response.json())
        .then((data) => {
          console.log("okkk====>",data)

          try{
            if(data===true){
              alert("Votre compte a été créé avec succès.");
              this.setState({message2:"Votre compte a été créé avec succès."});
              $("#success").css("display","block")
              this.setState({ok: <Redirect to='/login'/>});
            }else{
              this.setState({message:"Ce compte existe déjà, veuillez renseigner une autre adresse email."});
              $("#echec").css("display","block")
            }
          }catch(err){
            this.setState({message:"Ce compte existe déjà, veuillez renseigner une autre adresse email."});
            $("#echec").css("display","block")
            this.setState({ok: <Redirect to='/register'/>});
          }

          // this.setState({ok: <Redirect to='/login'/>});

        
        });

      }
    } 
  }

  handleDate= (date)=> {
  
   
    if(date.length!=0){
      
      this.setState({date:date[0]});
    }
  
    
  }

  handleChangeDocument = (e) => {
    console.log("adate =====>",e)
    try{
      this.setState({document_id:e.label})
     
    }catch(ee){

    }
  
  }
handleNom=(e)=>{
  console.log(e.target.value)
  try{
    if(e.target.value){
      $("#nom").removeClass("is-invalid")
    }else{
      $("#nom").addClass("is-invalid")
    }
   
  }catch(ee){
    $("#nom").addClass("is-invalid")
  }
}

handlePrenom=(e)=>{
  console.log(e.target.value)
  try{
    if(e.target.value){
      $("#prenom").removeClass("is-invalid")
    }else{
      $("#prenom").addClass("is-invalid")
    }
   
  }catch(ee){
    $("#prenom").addClass("is-invalid")
  }
  
}


  handleEmail=(e)=>{
    try{
      if(e.target.value){
        $("#email").removeClass("is-invalid")
      }else{
        $("#email").addClass("is-invalid")
      }
    
    }catch(ee){
      $("#email").addClass("is-invalid")
    }
  }
  
  handleMDP1=(e)=>{
    try{
      if(e.target.value){
        $("#password").removeClass("is-invalid")
      }else{
        $("#password").addClass("is-invalid")
      }
    
    }catch(ee){
      $("#password").addClass("is-invalid")
    }
  }

  handleMDP2=(e)=>{
    try{
      if(e.target.value){
        $("#password2").removeClass("is-invalid")
      }else{
        $("#password2").addClass("is-invalid")
      }
    
    }catch(ee){
      $("#password2").addClass("is-invalid")
    }
  }

  // handleCheckCGU=(e)=>{
  //   try{
  //     if(e.target.value){
  //       $("#checkCGU").removeClass("is-invalid")
  //     }else{
  //       $("#checkCGU").addClass("is-invalid")
  //     }
    
  //   }catch(ee){
  //     $("#checkCGU").addClass("is-invalid")
  //   }
  // }

  handleTelephone=(e)=>{
    console.log(e.target.value)
    try{
      if(e.target.value){
        $("#telephone").removeClass("is-invalid")
      }else{
        $("#telephone").addClass("is-invalid")
      }
    
    }catch(ee){
      $("#telephone").addClass("is-invalid")
    } 
  }

  handleRetourAcccueil=()=>{
    this.setState({ok:<Redirect to='/accueil-visiteur'/>})
  }
  
  render() {

  const { pick } = this.state
  const animatedComponents = makeAnimated();

    return (
      <div className="c-app c-default-layout flex-row align-items-center mt-4">
        <CContainer>
          <button onClick={this.handleRetourAcccueil} className="btn btn-success mb-5" > <i class='bx bxs-home-smile' ></i>  Retour</button>
          <CRow className="justify-content-center">
            <CCol md="12" lg="10" xl="9">
              <CCard className="mx-4">
                <CCardBody className="p-4">
                  <CForm>
                    <h1>Inscrivez-vous</h1>
                      {this.state.ok}
                
                      <Row> 
                        <Col md="6" className="mt-2">
                          <small>Nom *</small>
                          <Input
                                className="form-control "
                                type="text"
                                id="nom"
                                placeholder="Nom"
                                onChange={this.handleNom}
                            />
                        </Col>
                        <Col md="6" className="mt-2">
                          <small>Prénom(s) *</small>
                          <Input
                                className="form-control"
                                type="text"
                                id="prenom"
                                onChange={this.handlePrenom}
                                placeholder="Prénom (s)"
                            />
                        </Col>
                      </Row>
                    
                      <Row className="mt-4"> 
                        <Col md="6" className="mt-2">
                          <small>Adresse</small>
                          <Input
                            className="form-control"
                            type="text"
                            id="adresse"
                            placeholder="Adresse"
                          />
                        </Col>
                        <Col md="6" className="mt-2">
                          <small>Email *</small>
                          <Input
                            className="form-control"
                            type="text"
                            id="email"
                            onChange={this.handleEmail}
                            placeholder="Email"
                          />
                        </Col>
                      </Row>

                      <Row className="mt-4">
                        <Col md="6" className="mt-2">
                          <small>Numéro de téléphone *</small>
                          <Input
                            className="form-control "
                            onChange={this.handleTelephone}
                            type="text"
                            id="telephone"
                            placeholder="Numéro de téléphone"
                          />
                        </Col>
                        <Col md="6" className="mt-2">
                          <small>Date de naissance</small>
                          <div className="docs-datepicker">
                            <InputGroup>
                              <Flatpickr
                                value={pick}
                                className="form-control d-block"
                                placeholder="Choisissez la date de naissance"
                                options={{
                                  locale:French,
                                  // mode: "range",
                                  altInput: true,
                                  // altFormat: "F j, Y",
                                  altFormat: "d/M/Y"
                                }}
                                onChange={this.handleDate}
                              />
                              <div className="input-group-append">
                                <button
                                  type="button"
                                  className="btn btn-outline-secondary docs-datepicker-trigger"
                                  disabled
                                >
                                  <i
                                    className="fa fa-calendar"
                                    aria-hidden="true"
                                  />
                                </button>
                              </div>
                            </InputGroup>
                            <div className="docs-datepicker-container" />
                          </div>
                        </Col>
                      </Row>

                      <Row className="mt-4 mb-3"> 
                        <Col md="6" className="mt-2">
                          <small>Type de document d'identité</small> 
                          <ReactSelect
                            closeMenuOnSelect={true}
                            components={animatedComponents}
                          
                            onChange={this.handleChangeDocument}
                            options={this.state.document}

                            placeholder="Choisir le document"
                        />
                        </Col>

                        <Col md="6" className="mt-2">
                          <small>Numéro d'identité</small>
                          <Input
                            className="form-control"
                            type="text"
                            id="numeroId"
                            placeholder="Numéro d'identité"
                          />
                        </Col>
                      </Row>
                        
                      <div class="form-check">
                        <input 
                          class="form-check-input" 
                          checked="true"
                          required  
                          type="checkbox" 
                          value="" 
                          id="checkCGU"
                          // onChange={this.handleCheckCGU}
                          // onchange={this.setCustomValidity(validity.valueMissing ? 'Please indicate that you accept the Terms and Conditions' : '')}
                        />
                        <label class="form-check-label" for="flexCheckDefault">
                          J'accepte les <a href="/#/cgu"><u>Conditions générales d'utilisation de O-Terrain </u></a>.
                        </label>
                      </div>
  
                      <Row className="mt-4 mb-4">
                        
                        <Col md="6" className="mt-2">
                          <small>Mot de passe *</small>
                          <CInputGroup className="mb-3">
                            <CInputGroupPrepend>
                              <CInputGroupText>
                                <CIcon name="cil-lock-locked" />
                              </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput 
                              type="password" 
                              className="form-control " 
                              id="password" 
                              placeholder="Mot de passe" 
                              autoComplete="new-password"
                              onChange={this.handleMDP1}
                            />
                          </CInputGroup>
                        </Col>
                        
                        <Col md="6" className="mt-2">
                          <small>Répetez le mot de passe *</small>
                          <CInputGroup className="mb-4">
                            <CInputGroupPrepend>
                              <CInputGroupText>
                                <CIcon name="cil-lock-locked" />
                              </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput 
                              type="password" 
                              className="form-control " 
                              id="password2" 
                              placeholder="Répétez le mot de passe" 
                              onChange={this.handleMDP2}
                              // autoComplete="new-password" 
                            />
                          </CInputGroup>
                        </Col>
                      </Row>

                      <Row className="mt-4">
                        <Col md="12">
                          <div style={{textAlign:"center"}} id="success" class="alert alert-success" role="alert">
                            {this.state.message2}
                          </div>
                        </Col>  
                      </Row>

                      <Row className="mt-4">
                        <Col md="12">
                          <div style={{textAlign:"center"}} id="echec" class="alert alert-danger" role="alert">
                            {this.state.message}
                          </div>
                        </Col>  
                      </Row>

                    <CButton color="success" type="submit" onClick={this.handleAddUser} block>Créer mon compte</CButton>
                  </CForm>
                </CCardBody>

               

              </CCard>

            </CCol>
          </CRow>
        </CContainer>
      </div>
    )
  }
}

export default Register
